<script>
import { ref, onBeforeMount } from "vue";
import { useStore } from "vuex";
import { auth } from "../firebase";
import HeaderPage from "../components/HeaderPage.vue";
import Nextsongs from "../data/data.nextTracks";
import songs from "../data/data.tracks";
import { shuffleArray } from "../helpers/utils";
export default {
  components: { HeaderPage },
  setup() {
    const name = ref("");
    const store = useStore();
    onBeforeMount(() => {
      const user = auth.currentUser;
      if (user) {
        name.value = user.email.split("@")[0];
      }
      store.dispatch("fetchUser");
    });

    return {
      name,
    };
  },
  data() {
    return {
      current: {},
      circleLeft: null,
      barWidth: null,
      duration: null,
      currentTime: null,
      index: 0,
      isPlaying: false,
      isVisible: false,
      Nextsongs: shuffleArray(Nextsongs),
      songs: shuffleArray(songs),
      player: new Audio(),
    };
  },

  methods: {
    favorite() {
      this.songs.push({ img: this.current.img, title: this.current.title, artists: this.current.artists, src: this.current.src });
    },
    play(song) {
      if (typeof song.src != "undefined") {
        this.current = song;

        this.player.src = this.current.src;
      }

      this.player.play();
      this.player.addEventListener(
        "ended",
        function () {
          this.index++;
          if (this.index > this.Nextsongs.length - 1) {
            this.index = 0;
          }

          this.current = this.Nextsongs[this.index];

          this.play(this.current);
        }.bind(this)
      );
      this.isPlaying = true;
    },
    pause() {
      this.player.pause();
      this.isPlaying = false;
    },
    prev() {
      this.index--;
      if (this.index < 0) {
        this.index = this.Nextsongs.length - 1;
      }

      this.current = this.Nextsongs[this.index];

      this.play(this.current);
    },
    next() {
      this.index++;
      if (this.index > this.Nextsongs.length - 1) {
        this.index = 0;
      }

      this.current = this.Nextsongs[this.index];

      this.play(this.current);
    },
    generateTime() {
      let width = (100 / this.player.duration) * this.player.currentTime;
      this.barWidth = width + "%";
      this.circleLeft = width + "%";
      let durmin = Math.floor(this.player.duration / 60);
      let dursec = Math.floor(this.player.duration - durmin * 60);
      let curmin = Math.floor(this.player.currentTime / 60);
      let cursec = Math.floor(this.player.currentTime - curmin * 60);
      if (durmin < 10) {
        durmin = "0" + durmin;
      }
      if (dursec < 10) {
        dursec = "0" + dursec;
      }
      if (curmin < 10) {
        curmin = "0" + curmin;
      }
      if (cursec < 10) {
        cursec = "0" + cursec;
      }
      this.duration = durmin + ":" + dursec;
      this.currentTime = curmin + ":" + cursec;
    },
    updateBar(x) {
      let progress = this.$refs.progress;
      let maxduration = this.player.duration;
      let position = x - progress.offsetLeft;
      let percentage = (100 * position) / progress.offsetWidth;
      if (percentage > 100) {
        percentage = 100;
      }
      if (percentage < 0) {
        percentage = 0;
      }
      this.barWidth = percentage + "%";
      this.circleLeft = percentage + "%";
      this.player.currentTime = (maxduration * percentage) / 100;
      this.player.play();
    },
    clickProgress(e) {
      this.isPlaying = true;
      this.player.pause();
      this.updateBar(e.pageX);
    },

    toggleVisibility() {
      this.isVisible = !this.isVisible;
    },
    resetPlayer() {
      this.barWidth = 0;
      this.circleLeft = 0;
      this.player.currentTime = 0;
      this.player.src = this.current.source;
      setTimeout(() => {
        if (this.isPlaying) {
          this.player.play();
        } else {
          this.player.pause();
        }
      }, 300);
    },
  },
  created() {
    this.current = this.Nextsongs[this.index];
    this.player.src = this.current.src;

    let vm = this;
    this.player.ontimeupdate = function () {
      vm.generateTime();
    };
    this.player.onloadedmetadata = function () {
      vm.generateTime();
    };
    this.player.onended = function () {
      vm.next();
      this.isPlaying = true;
    };
  },
};
</script>
